"use client";

import { useRouter } from "next/navigation";
import { useEffect, useState } from "react";

interface Design {
  id: string;
  title: string;
  description: string;
  imageUrl: string;
}

const designs: Design[] = [
  {
    id: "modern-workspace",
    title: "Modern Workspace",
    description: "A sleek and modern workspace design.",
    imageUrl:
      "https://images.pexels.com/photos/3184293/pexels-photo-3184293.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=940",
  },
  {
    id: "creative-studio",
    title: "Creative Studio",
    description: "A vibrant and creative studio environment.",
    imageUrl:
      "https://images.pexels.com/photos/276452/pexels-photo-276452.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=940",
  },
  {
    id: "minimalist-office",
    title: "Minimalist Office",
    description: "A clean and minimalist office space.",
    imageUrl:
      "https://images.pexels.com/photos/374074/pexels-photo-374074.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=940",
  },
];

export default function DesignDetailPage({ params }: { params: { id: string } }) {
  const [design, setDesign] = useState<Design | null>(null);
  const router = useRouter();

  useEffect(() => {
    // Redirect to drafts dashboard page on design detail click
    router.push("/dashboard/drafts");
  }, [router]);

  if (!design) {
    return <div>Loading...</div>;
  }

  return (
    <main className="min-h-screen bg-gray-50 p-8">
      <div className="max-w-4xl mx-auto">
        <h1 className="text-4xl font-bold mb-4">{design.title}</h1>
        <img
          src={design.imageUrl}
          alt={design.title}
          className="w-full rounded-lg shadow-lg mb-6"
        />
        <p className="text-lg text-gray-700">{design.description}</p>
      </div>
    </main>
  );
}
